html, body, .styled-container, #app-container {
  height: 100%;
  margin: 0;
  display: flex;
  flex-direction: column;
  font-family: 'Arial', sans-serif;
  background-color: #f8f9fa;
  color: #333;
}

main {
  flex: 1;
}

#about section {
  font-size: 1.1rem;
  line-height: 1.6;
}

body.dark-mode {
  background-color: #121212;
  color: white;
}

body.dark-mode .toggle-label {
  color: #ffc107;
}

.dark-mode a {
  color: #4da6ff;
}

.navbar {
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

#header-logo {
  height: 40px;
  width: auto;
}

.navbar-brand {
  font-size: 1.5rem;
  font-weight: bold;
}

.navbar-nav .nav-link {
  font-size: 1.1rem;
  padding: 10px 15px;
}

.navbar-nav .nav-link.active {
  font-weight: bold;
  color: #f8f9fa !important;
}

.container {
  /* max-width: 900px; */
  flex: 1;
}

h2 {
  font-size: 2rem;
  font-weight: bold;
  text-align: center;
  margin-bottom: 20px;
  color: #007bff;
}

.btn {
  padding: 10px 15px;
  font-size: 1rem;
  border-radius: 5px;
  transition: all 0.3s ease-in-out;
}

.btn-primary {
  background-color: #007bff;
  border: none;
}

.btn-primary:hover {
  background-color: #0056b3;
}

.btn-success {
  background-color: #28a754;
  border: none;
}

.btn-success:hover {
  background-color: #1e7e34;
}

/* .rounded-circle { */
  /* border: 4px solid #007bff; */
/* } */

.dark-mode a {
  color: #4da6ff;
}

.dark-mode .bg-light,
.dark-mode .bg-white {
  background: #1e1e1e;
}

.dark-mode .styled-list li::before {
  color: #ffc107;
}

.about-text {
  font-size: 1.1rem;
  line-height: 1.6;
}

.filter-btn {
  background: #333;
  color: white;
  border: none;
  padding: 8px 12px;
  margin: 5px;
  cursor: pointer;
  border-radius: 5px;
  font-size: 14px;
  transition: background 0.3s;
}

.filter-btn.active, .filter-btn:hover {
  background: #555;
}
/* class="projects-grid" */
.projects-grid {
  /* display: grid; */
  /* grid-template-columns: repeat(auto-fit); */
  gap: 20px;
  padding: 20px;
}

.projects-grid .card {
/* .card { */
  background: white;
  border-radius: 10px;
  padding: 15px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease-in-out;
  text-align: center;
}

.dark-mode .card {
  background-color: #1e1e1e;
  color: white;
}

.projects-grid .card:hover {
  transform: translateY(-5px);
}

.projects-grid .card-title {
  font-size: 1.3rem;
  margin-bottom: 10px;
}

.projects-grid .card img {
  max-height: 250px;
  object-fit: contain;
  border-radius: 10px;
}

.projects-grid .card-text {
  font-size: 1rem;
  color: #666;
}

.projects-grid .card a {
  text-decoration: none;
  font-weight: bold;
  counter-reset: #007bff;
}

.projects-grid .card a:hover {
  text-decoration: underline;
}

.social-links {
  margin-top: 15px;
}

.social-links a {
  margin-right: 10px;
}

form .form-control {
  border-radius: 5px;
  border: 1px solid #ccc;
  padding: 10px;
}

form button {
  background-color: #333;
  color: white;
  padding: 10px 15px;
  border-radius: 5px;
  border: none;
  cursor: pointer;
}

form button:hover {
  background-color: #555;
}

footer {
  background-color: #222;
  color: white;
  text-align: center;
  padding: 15px 0;
  margin-top: auto;
  flex-shrink: 0;
}

.shadow-sm {
  box-shadow: 8px 4px 6px rgba(0, 0, 0, 0.1);
}

.rounded {
  border-radius: 10px;
}

.bg-light {
  background: linear-gradient(to right, #f8f9fa, #e9ecef);
}

.bg-white {
  background: linear-gradient(to right, #ffffff, #f1f1f1);
}

.styled-list {
  list-style: none;
  padding-left: 0;
}

.styled-list li {
  padding: 8px, 0;
  font-size: 1.1rem;
}

.styled-list li::before {
  content: "âœ”";
  color: #007bff;
  font-weight: bold;
  margin-right: 8px;
}


.dark-mode-toggle {
  display: flex;
  align-items: center;
  margin-left: 15px;
}

.dark-mode-toggle input {
  display: none;
}

.toggle-label {
  font-size: 1.5rem;
  cursor: pointer;
  padding: 5px 10px;
  border-radius: 20px;
  color: #f8f9fa;
  transition: color 0.3s ease, background 0.3 ease;
}

.toggle-label:hover {
  opacity: 0.8;
}